.rightInfo {
	.upload{
		clear: both;
		.a-upload {
		    padding: 4px 10px;
		    height: 30px;
		    line-height: 20px;
		    position: relative;
		    cursor: pointer;
		    color: #888;
		    background: #fafafa;
		    border: 1px solid #ddd;
		    border-radius: 4px;
		    overflow: hidden;
		    display: inline-block;
		}

		.a-upload  input {
		    position: absolute;
		    font-size: 20px;
		    right: 0;
		    top: 0;
		    opacity: 0;
		    filter: alpha(opacity=0);
		    cursor: pointer
		}

		.a-upload:hover {
		    color: #444;
		    background: #eee;
		    border-color: #ccc;
		    text-decoration: none
		}

		button {
			vertical-align: top;
	    height: 30px;
	    border: none;
	    display: inline-block;
	    padding: 0 20px;
	    border-radius: 4px;
	    color: #999;
	    margin-left: 10px;
		}

		button:hover {
			color: #666;
		}
	}

	.submitBtn{
		display: inline-block;
    height: 30px;
    padding: 0 20px;
    border: none;
    border-radius: 4px;
    background-color: #4ca0b3;
    color: #FFF;
	}
}